body {
  background: rgb(0,0,0);
  background: linear-gradient(90deg, rgba(0,0,0,1) 0%, rgba(94,9,121,1) 100%);
  padding: 20px;
}

h1, h2 {
  color: #fff;
  margin: 20px 0;
  text-align: center;
}

input {
  border: none;
  border-radius: 50px;
  font-weight: 800;
  outline: none;
  padding: 5px 5px 5px 20px;
  text-align: center;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.style-card {
  max-width: 20rem;
  margin: 4px;
}

@supports(object-fit: cover){
  img {
    height: 100%;
    object-fit: cover;
    object-position: center center;
  }
}
